@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&family=Roboto:ital,wght@0,400;0,500;1,400&display=swap');

body {
    font-size: 16px;
    font-family: 'Nanum Gothic', sans-serif;
    /* font-family: 'Roboto', sans-serif; */
}

.cf:after{
  display: block;
  content: '';
  clear: both;
}

img{
  width: 100%;
  vertical-align: middle;
}

button{
  cursor: pointer;
  border: none;
}

fieldset{
  display: none;
}

legend{
  display: none;
}

h1,h2,h3,h4,h5,h6{
  font-size: 100%;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

a{
  color: inherit;
  text-decoration: none;
}

ul , ol{
  list-style: none;
}

.row{
  max-width:1600px;
  margin: 0 auto;
  padding: 0 80px;
}

table{
  width: 100%;
  border-collapse :collapse;
  table-layout: fixed;  
  /* table-layout: fixed; 테이블들의 콘텐츠들이 자동으로 줄바꿈 부모테이블의 칸넓이가 꺠지지 않도록 white-space: nowrap; 을 쓰면무너지는걸 방지 */
}
.blind{
  position: absolute;
  width: 0px;
  height: 0px;
  overflow: hidden;
  text-indent: -99999999999999999px;
}