.headerwrap{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    color: #fff;
    transition: 0.5s all;
    z-index: 9;
    .row{
        position: relative;
        display:flex;
        height: 80px;
        justify-content: space-between;
        align-items: center;     // 중간정렬
        .logo, .nav, .mobileNav{
        }
        .mobileNav{
            position: relative;
            button{
                padding: 10px 10px;
                border-radius:50px;
                border: none;
                display: flex;  //ham, person 의 얼라인 센터 플렉스
                font-size: 2vw;
                cursor: pointer;
                .ham{
                    align-self: center;     // 중간정렬
                    color: #717171;
                    width: 3vw;
                }
                .person{
                    color: #717171;
                    margin-left: 5px;
                    align-self: center;     // 중간정렬
                    width: 3vw;
                }
            }
            .mobileNavList{
                background-color: #fff;
                position: absolute;
                margin-top: 10px;
                top: 100%;
                right: 0;
                color: #000;
                width: 150px;
                border-radius: 10px;
                padding: 10px 0;
                display: none;
                &.opener{   // .mobileNavList.opener
                    display: block;
                    transition: 0.5s all;
                }
                ul{
                    li{
                        line-height: 30px;
                        padding: 0 20px;
                    }
                    li:nth-child(3){
                        border-top: 1px solid #ddd;
                        margin-top: 10px;
                        padding-top: 10px;
                        
                    }
                }
            }
        }
        .nav{
            position: absolute;
            left: 50%;
            transform: translate(-50%);
            .depth1{
                display: flex;
                // background-color: red;
                li{
                    color: #fff;
                    position: relative;
                    padding: 20px 0;
                    cursor: pointer;
                    margin: 0 30px;
                    .depth2{
                        display: none;
                        background: #fff;
                        position: absolute;
                        top: 100%;
                        left: 0;
                        width: 100px;
                        &.on{
                            display: block;
                        }
                        li{
                        color: #000;
                        padding-bottom: 10px;
                        }
                    }
                }
            }
        }

    }
}