.homesecWrap{
    .article1{
        z-index: 1;
        position: relative;
        .slide{
            position: relative;
            height:45vw !important;
            background-size:cover;
            background-repeat:no-repeat;
            background-position:center;
            margin-top:4.7vw;
            .text{
                position:absolute; left:50%; text-align:center;
                top:50%; transform:translate(-50%, -50%);
                color:#fff; font-size:50px;
                p{
                    margin-top: 20px;
                }
                button{
                    padding: 10px;
                    border: none;
                    color: #000;
                    background: #fff;
                    border-radius: 10px;
                    &:hover{
                        background: red;
                        color: lime;
                    }
                }
            }
        };

        .slide1{
            span{  
                padding: 10px;
                border-radius: 30px;
                color:#000;
                background-color:#fff;
                font-size: 3vw;
            };
        }
        .slide2{
            span{  
                padding: 10px;
                border-radius: 30px;
             
                color:#000;
                // display:block;
                background-color:#fff;
                font-size: 3vw;
            };
            p{
                text-align: center;
                a{
                color:#000;
                background-color:#fff;
                    &:hover{
                        color:#fff;
                        background-color:#000;
                        }
                }
            }
        }
        .slide3{
            span{  
                padding: 10px;
                border-radius: 30px;
              
                color:#000;
                // display:block;
                background-color:#fff;
                font-size: 3vw;
            };
            a{
                color:#000;
                background-color:#fff;
                &:hover{
                color:#fff;
                background-color:#000;
                }
            }
        }
    }


    .about_me{
        color: #4991a8;
        position: relative;
        h3{
            font-size: 2.5vw;
            margin-bottom: 15px;
        }
        p{
            font-size: 1.5vw;
        }
        div{
            width: 100%;
        }
       .my{
           position: absolute;
           display: flex;
           top: 10%;
           left: 5%;
           align-items: center;
           width: 50%;
           .my_flex{
               width: 30%;
               display: flex;
               flex-direction: column;
               margin-left: 10px;
           }
           img{
               width: 6.5vw;
           }
           h3{
            font-size: 2.5vw;
           }
           p{
            font-size: 1.5vw;
           }
       }
       .skils{
        position: absolute;
        bottom: 10%;
        left: 5%;
        width: 20%;
       }
       .introduce{
        position: absolute;
        top: 10%;
        right: 5%;
        width: 20%;
       }
       .address{
        position: absolute;
        top: 50%;
        right: 0;
        width: 25%;
       }
       .school{
        position: absolute;
        bottom: 10%;
        right: 0;
        width: 30%;
       }
    }
    position: relative;
    .article2{
        display: flex;
        .row{
            ul{display: flex;
                flex-wrap: wrap;    //줄바꿈 정렬
                padding:30px 0;
                li{
                    padding:20px;
                    width: 33.33%;
                    a{
                        display: flex;
                        img{
                            width: 30%;
                            margin-right: 10px ;
                            border-radius: 10px;
                        }
                        div{
                            align-self: center;
                        }
                    }
                    
                }
            }
            .lifeStyle{
                h2{
                    font-size: 20px;
                }
                ul{display: flex;
                    flex-wrap: wrap;    //줄바꿈 정렬
                    padding:30px 0;
                    li{
                        padding:20px;
                        width: 33.33%;
                        
                        img{
                            border: solid 0.5px #000;
                            width: 100%;
                            margin-right: 10px ;
                            border-radius: 10px;
                        }
                    }
                }
            }
        }
    }

    .article3{
        .row{
            .multi_rolling{
                .multi_visual{
                    .multi_slide{
                        img{
                        }
                    }
                }
            }
        }
    }
    
}